.how {
  padding-top: 50px;

  @include mq(desktop) {
    padding-top: 130px;
    padding-bottom: 130px;
  }
}

.how__title {
  text-align: center;

  margin-bottom: 30px;
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;

  color: #111518;

  @include mq(tablet) {
    font-size: 26px;
  }

  @include mq(desktop) {
    text-align: left;
    margin-bottom: 50px;
    font-size: 46px;
  }
}

.how__list {
  @include mq(tablet) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 27px 20px;
  }

  @include mq(desktop) {
    gap: 20px 20px;
  }
}

.how__item {
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 30px;
  padding-bottom: 25px;
  background-color: #ffffff;
  box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
  transition: color 250ms ease, background-color 250ms ease, fill 250ms ease, transform 450ms ease;

  @include mq(mobile-only) {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  @include mq(tablet) {
    width: 334px;

    display: flex;
    flex-direction: column;

    padding-top: 40px;
    padding-bottom: 53px;
  }

  @include mq(desktop) {
    padding-top: 40px;
    padding-bottom: 106px;

    width: 425px;
  }
}

.how__item:hover,
.how__item:focus {
  background-color: #ff6b09;
  cursor: pointer;
  transform: scale(1.02);
}

.how__item:hover .how__icon,
.how__item:focus .how__icon {
  fill: #ffffff;
}

.how__item:hover .how__item-text,
.how__item:focus .how__item-text {
  color: #ffffff;
}

.how__item:hover .how__item-title,
.how__item:focus .how__item-title {
  color: #ffffff;
}

.how__item:hover .how__item-text::before,
.how__item:focus .how__item-text::before {
  background-color: #ffffff;
}

.how__icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;

  fill: #ff6b09;
  transition: fill 250ms ease;
}

.how__item-title {
  margin-left: auto;
  margin-right: auto;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;

  text-align: center;
  letter-spacing: 0.08em;

  color: #111518;

  transition: color 250ms ease;

  @include mq(tablet-only) {
    padding-left: 35px;
    padding-right: 35px;
  }

  @include mq(desktop) {
    font-size: 20px;
    line-height: 1.4;
    max-width: 259px;
    flex-grow: 0;
  }
}

.how__item-title--last {
  @include mq(desktop) {
    max-width: 365px;
  }
}

.how__item-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  text-align: center;
  letter-spacing: 0.08em;

  color: #b9b9b9;
  margin-top: 19px;

  transition: color 250ms ease;

  @include mq(tablet) {
    margin-top: 24px;

    padding-right: 20px;
    padding-left: 20px;
  }

  &::before {
    content: '';
    display: block;
    width: 20px;
    height: 2px;
    background-color: #ff6b09;
    margin-left: auto;
    margin-right: auto;

    margin-bottom: 19px;

    transition: background-color 250ms ease;

    @include mq(tablet) {
      font-size: 18px;
      line-height: 1.56;
      margin-bottom: 23px;
    }
  }

  @include mq(desktop) {
    font-size: 18px;
    line-height: 1.56;
    padding-left: 30px;
    padding-right: 30px;
  }
}
